@import url(https://fonts.googleapis.com/css?family=Noto+Serif+JP:600|Playfair+Display:900&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 250vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    margin: 0;
    padding: 0;
}

.App-header {
    width: 100%;
    background: #60FF00;
    margin: 0;
    padding: 2vh 0;
    vertical-align: middle;
    text-align: center;
}

.App-header h1{
    font-family: 'Franklin Gothic';
    font-size: 10vw;
    font-weight: bold;
    padding: 0;
    margin: 0;
    color: #000000;
}

.App-footer {
    margin: 5vw 0;
}

.App-footer h2 span{
    color: #000000;
    font-size: 8vw;
    background-color: yellow;
}

.header-title{
    font-family: 'Heebo';
    font-weight: 100;
    font-size: 9vw;
    line-height: 1.5em;
    color: #aaaaaa;
    margin: 0;
    padding: 0;
}

.JapaneseText{
    margin: 2.5vw 0;
}

.JapaneseText .wrapper{
    margin: 0;
    padding: 0 1vw 0 4vw;
    -webkit-writing-mode: vertical-rl;
        -ms-writing-mode: tb-rl;
            writing-mode: vertical-rl;
    -webkit-text-orientation: upright;
            text-orientation: upright;
    width: 90vw;
    height: auto;

    /* -webkit-column-width: 23em; /\* Chrome, Safari, Opera *\/ */
    /* -webkit-column-gap: 2rem; */
    /* -moz-column-width: 23em; /\* Firefox *\/ */
    /* -moz-column-gap: 2rem; */
    -webkit-column-width: 21em;
            column-width: 21em;
    /* column-gap:   2em; */

    background-color: #33FFFF;
    border: 4px solid #000000;
}

.JapaneseText .wrapper p{
    font-family: 'Noto Serif JP';
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 0.045em;
    line-height: 1.65em;
    margin: 0 1em;
    /* height: 25em; */
}

